<template>
  <el-dialog
    title="添加角色"
    v-model="dialogVisible"
    width="30%"
    :before-close="handleClose"
  >
    <el-form ref="formRef" :model="form" :rules="rules" style="width: 100%;" label-position="top">
      <el-form-item label="用户名" prop="name">
        <el-input v-model="form.name" clearable placeholder="请输入用户名" />
      </el-form-item>
      <el-form-item style="margin-bottom: 30px;" label="密码" prop="pass">
        <el-input v-model="form.pass" type="password" clearable placeholder="请输入密码" />
      </el-form-item>
      <el-form-item prop="role" label="角色">
        <el-select v-model="form.role" style="width: 100%;" placeholder="请选择角色">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
    </el-form>
    <div class="footer">
      <el-button @click="handleClose">取消</el-button>
      <el-button type="primary" @click="addHandle">保存</el-button>
    </div>
  </el-dialog>
</template>
<script>
import { ref, reactive, watch } from 'vue'
import { ElMessage } from 'element-plus'
import { rules, options } from './config'
export default {
  props: {
    visible: Boolean,
    detail: Object
  },
  setup(props, { emit }) {
    const formRef = ref()
    const dialogVisible = ref(false)
    const form = reactive({
      name: '',
      pass: '',
      role: '',
    })
    const handleClose = () => {
      emit('handleClose')
    }
    const addHandle = () => {
      formRef.value && formRef.value.validate(val => {
        if (val) {
          ElMessage.success('保存成功')
          formRef.value && formRef.value.resetFields()
          emit('handleClose')
        }
      })
    }
    watch(
      () => props.visible,
      val => {
        dialogVisible.value = val
        if (val) {
          const { name, pass, role } = props.detail ?? {}
          form.name = name
          form.pass = pass
          form.role = role
        }
      }
    )
    return {
      form,
      formRef,
      rules,
      options,
      dialogVisible,
      addHandle,
      handleClose,
    }
  },
}
</script>
<style lang="scss" scoped>
.footer {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
