<template>
  <div>
    <div class="top">
      <el-button type="primary" @click="addHandle">新增</el-button>
    </div>
    <el-table
      :data="dataSource"
      style="width: 100%"
    >
      <el-table-column prop="code" label="账号" />
      <el-table-column prop="name" label="姓名" />
      <el-table-column prop="phone" label="手机号" />
      <el-table-column prop="emial" label="邮箱" />
      <el-table-column prop="time" label="创建时间" />
      <el-table-column prop="user" label="角色" />
      <el-table-column prop="oprate" label="操作">
        <template #default="{row}">
          <div>
            <el-link  style="margin-right: 10px;" type="primary" @click="deleteHandle(row)">删除</el-link>
            <el-link  style="margin-right: 10px;" type="primary" @click="resetHandle(row)">重置密码</el-link>
            <el-link type="primary" @click="editHandle(row)">编辑</el-link>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <Modal
      :detail="detail"
      :visible="visible"
      @handleClose="handleClose"
    />
  </div>
</template>
<script>
import { ref } from 'vue'
import Modal from './modal'
import { ElMessage } from 'element-plus'

export default {
  components: {
    Modal
  },
  setup() {
    const visible = ref(false)
    const detail = ref(null)
    const dataSource = ref([
      { code: 'asdasd', name: '长河', phone: '13121212', emial: '163@.com', time: '2021-01-02', user: '胜多负少' }
    ])

    const addHandle = () => {
      visible.value = true
    }
    const handleClose = () => [
      visible.value = false
    ]
    const editHandle = row => {
      visible.value = true
      detail.value = row
    }
    const deleteHandle = row => {
      ElMessage.success('删除成功')
    }
    const resetHandle = row => {
      ElMessage.success('重置成功')
    }

    return {
      visible,
      detail,
      dataSource,
      handleClose,
      addHandle,
      editHandle,
      deleteHandle,
      resetHandle
    }
  },
}
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>
